import React, { useState } from "react";
import { useTranslation } from "react-i18next";
import market from "../../../../config/market";

function Toolbox({ setCols, pages, cols, setQuery }) {
	const { t } = useTranslation();
	const [activeFilter, setActiveFilter] = useState(0);

	function changeFilter(value) {
		setActiveFilter(value);
		setQuery((prevQ) => ({
			...prevQ,
			order: value,
		}));
	}
	function changeFilterAZ(e) {
		setQuery((prevQ) => ({
			...prevQ,
			order: e.target.value,
		}));
	}

	return (
		<div className="toolbox">
			<div className="toolbox-center">
				<div className="toolbox-info">
					{t("shown")}{" "}
					<span>
						{pages.variations.length} {t("from")} {pages.totalCount}
					</span>{" "}
					{t("products")}
				</div>
			</div>

			<div className="toolbox-right">
				<div className="toolbox-sort">
					<label htmlFor="sortby">{t("sort")}:</label>
					<ul className="sort-list d-flex align-items-center mb-0">
						<li
							onClick={() =>
								changeFilter(
									activeFilter === "sellPrice asc"
										? "sellPrice desc"
										: "sellPrice asc"
								)
							}
						>
							{t("price")}
							<span
								className={
									activeFilter === "sellPrice asc"
										? "icon-arrow-up"
										: "icon-arrow-down"
								}
							></span>
						</li>
					</ul>
					{market.config.home === 6 ||
					market.config.home === 3 ||
					market.config.home === 9 ? (
						<>
							<label htmlFor="sortby sort-by-name">
								{t("sort_by_name")}:
							</label>
							<div className="select-custom">
								<select
									name="order"
									id="sortby"
									onChange={changeFilterAZ}
									className="form-control mb-0"
								>
									<option value="name asc">
										{t("titleAZ")}
									</option>
									<option value="name desc">
										{t("titleZA")}
									</option>
									<option value="sellPrice asc">
										{t("cheaper")}
									</option>
									<option value="sellPrice desc">
										{t("expensive")}
									</option>
								</select>
							</div>
						</>
					) : null}
				</div>
			</div>

			<div className="toolbox-layout">
				<button
					className={`btn-layout ${"4cols" === cols ? "active" : ""}`}
					onClick={(e) => {
						setCols("4cols");
					}}
				>
					<img src={market.config.icons.layout} alt="layout" />
				</button>
				<button
					className={`btn-layout ${
						"12cols" === cols ? "active" : ""
					}`}
					onClick={(e) => {
						setCols("12cols");
					}}
				>
					<img src={market.config.icons.layout_} alt="layout_" />
				</button>

				<span
					className="filter d-none"
					onClick={() =>
						window.document
							.querySelector("body")
							.classList.add("sidebar-filter-active")
					}
				>
					<span>{t("filter")}</span>
					<img src={market.config.icons.filter} alt="filter" />
				</span>
			</div>
		</div>
	);
}

export default Toolbox;
