import React, { useEffect, useState } from "react";
import { Link } from "react-router-dom";
import { getBrandAttribute } from "../../../utils/oxMethods";
import { formatterCurrency } from "../../../utils/string";
import { connect, useDispatch, useSelector } from "react-redux";
import { getVariation } from "../../../api/products";
import { setChoosenProduct } from "../../../actions/choosenProducts";
import MediaImage from "./MediaImage";

function ProductCardRow(props) {
	const { product, haveSupplier, settings } = props;

	const dispatch = useDispatch();

	const brandAttribute = getBrandAttribute(product, settings);
	const [supplier, setSupplier] = useState();
	const { productList } = useSelector(state => state.choosenProducts);
	const haveChoosen = productList.find(p => p.stock.id === product.stock.id);

	useEffect(() => {
		if (haveSupplier) {
			getVariation(product.variation.id).then(res => {
				setSupplier(res.suppliers[0]);
			});
		}
	}, [haveSupplier]);

	let image = product?.variation?.images[0];

	function toTop() {
		window.scroll({
			top: 0
		});
	}

	return product ? (
		<div className="card-row">
			<div className="d-flex">
				<div className="product-header">
					<Link
						to={`/product/${product.variation.id}`}
						onClick={toTop}
					>
						<MediaImage
							image={image}
							largeSize={"300x_"}
							mediumSize={"150x_"}
							smallSize={"150x_"}
							alt={product.name}
						/>
					</Link>

					<button
						className="btn-heart"
						onClick={() => dispatch(setChoosenProduct(product))}
						style={{
							color: haveChoosen ? "red" : ""
						}}
					>
						{haveChoosen ? (
							<img
								src="/assets/images/vibo/choosen-heart.svg"
								alt="heart"
							/>
						) : (
							<img
								src="/assets/images/vibo/not-choosen-heart.svg"
								alt="heart"
							/>
						)}
					</button>
				</div>
				<div className="body-product">
					<h6 className="title-product">
						<Link to={"/product/" + product.variation.id}>
							{brandAttribute ? (
								<span className="supplier-name">
									{brandAttribute} /
								</span>
							) : null}
							{product.variation.name}
						</Link>
					</h6>

					<div className="price-product">
						<span className="price-new">
							{formatterCurrency(product.newPrice)}
						</span>
					</div>
					{supplier ? (
						<div className="product-supplier">
							<div className="supplier-img">
								<img
									src={
										supplier?.logo?.urls[
											"300x_" ||
												"150x_" ||
												"800x_" ||
												"100x_"
										]
									}
									alt={supplier.name}
								/>
							</div>
							<p>{supplier.name}</p>
						</div>
					) : null}
				</div>
			</div>
		</div>
	) : null;
}

export default connect(state => ({ settings: state.marketData.settings }))(
	ProductCardRow
);
