import React from "react";
import { SlideToggle } from "react-slide-toggle";
import market from "../../../../config/market";
import { dt } from "../../../../i18n";
import CheckboxGroup from "./Checkboxes/CheckboxGroup";
// import CheckboxGroupColor from "./Checkboxes/CheckboxGroupColor";

function Filters({ filters }) {
	function changeIcon(e) {
		e.target.classList.toggle("collapsed");
	}
	return filters[1]
		.filter(([idProp, dataProp]) => dataProp.choices.length !== 0)
		.map(([idProp, dataProp], index) => {
			const choices = Object.values(dataProp.choices);
			const choice = choices.find(
				choice => choice.settings && choice.settings.type
			);
			const type = choice && choice.settings && choice.settings.type;
			return (
				<SlideToggle
					collapsed={market.config.home === 9 ? true : false}
					key={index}
				>
					{({ onToggle, setCollapsibleElement }) => (
						<div
							className="widget widget-collapsible"
							style={{ margin: "0" }}
						>
							<h3
								className="widget-title"
								style={{
									margin: "10px 0"
								}}
							>
								<a
									href="#Size"
									onClick={e => {
										onToggle(e);
										changeIcon(e);
										e.preventDefault();
									}}
									role="button"
									className="collapsed"
									aria-expanded="true"
									aria-controls="widget-1"
								>
									{dt(dataProp.label)}
								</a>
							</h3>
							<div ref={setCollapsibleElement}>
								<div
									className="widget-body"
									style={{
										maxHeight: "300px",
										overflow: "auto"
									}}
								>
									{type === "color" ? (
										<div className="filter-colors filter-colors--color">
											<CheckboxGroup
												name={`${filters[0]}[${idProp}]`}
												options={choices}
											/>
										</div>
									) : (
										<div className="filter-items">
											<CheckboxGroup
												name={`${filters[0]}[${idProp}]`}
												options={choices}
											/>
										</div>
									)}
								</div>
							</div>
						</div>
					)}
				</SlideToggle>
			);
		});
}

export default Filters;
