import React, { useState, useEffect, Fragment } from "react";
import { useTranslation } from "react-i18next";
import store from "../../store";
import { setStatePages } from "../../actions/any";
import ProductCard from "../../components/features/product/";
import QuickView from "../../components/features/product/QuickViewNew";
import { addToCart } from "../../actions/cart";
import { getProductsPage } from "../../api/products";
import ToolboxThree from "../../components/features/product/details/ToolboxThree";
import FilterCanceler from "../../components/features/product/details/FilterCancelButton";
import SimpleShimmerProducts from "../../components/features/loaders/SimpleShimmerProducts";
import ChooseVariationModal from "../../components/features/modal/choose-variation";
import ProductCardRow from "../../components/features/product/ProductCardRow";
import Pagination from "../../components/features/product/details/Pagination";
import LoadMoreButton from "../../components/features/product/details/loadMoreButton";

const MORE_LOAD_PAGE_STEP = 3;
const grid = {
	"2cols": "col-6",
	"3cols": "col-6 col-md-4 col-lg-4",
	"4cols": "col-6 col-md-4 col-lg-4 col-xl-3",
	"5cols": "col-6 col-md-4 col-lg-3 col-xl-5col",
	"12cols": "col-12"
};
const units = {
	"2cols": 6,
	"3cols": 9,
	"4cols": 12,
	"5cols": 15
};
const queryString = require("qs");
const initialPages = {
	page: 1,
	totalCount: 0,
	variations: [],
	hasMore: true,
	allPages: 1
};

function MarketProductList({ menu, title, query, setQuery, column }) {
	const { t } = useTranslation();
	const [cols, setCols] = useState(column);
	const [loading, setLoading] = useState(true);
	const [pages, setPages] = useState(initialPages);
	const [modal, setModal] = useState({
		open: false,
		product: { variation: { id: 0 } }
	});

	function fetchMoreData(load_more = false) {
		if (pages.totalCount === pages.variations.length) {
			setPages(prevPage => ({
				...prevPage,
				hasMore: false
			}));
			return;
		}
		if (!load_more && pages.page % MORE_LOAD_PAGE_STEP === 0) {
			setPages(prevPage => ({
				...prevPage,
				hasMore: false,
				more_load_time: true
			}));
			return;
		}
		query.page = parseInt(query.page) + 1;
		const strQuery = queryString.stringify(query);
		getProductsPage(menu.id, strQuery)
			.then(page => {
				window.history.replaceState(
					null,
					title,
					`/category/${menu.slug}?` + strQuery
				);
				setPages(prevPage => ({
					page: parseInt(page.page),
					totalCount: page.totalCount,
					variations: prevPage.variations.concat(page.variations),
					hasMore: page.variations.length > 0
				}));
			})
			.catch(err => {
				setPages(prevPage => ({
					...prevPage,
					hasMore: false
				}));
			});
	}

	function onAddToCart(dataProduct) {
		setModal({
			open: true,
			product: dataProduct
		});
	}
	useEffect(() => {
		let mount = true;
		setLoading(true);
		const strQuery = queryString.stringify(query);
		getProductsPage(menu.id, strQuery)
			.then(page => {
				if (mount) {
					window.history.replaceState(
						null,
						title,
						`/markets/${menu.slug}?` + strQuery
					);
					setPages(prevPage => ({
						page: prevPage.page,
						totalCount: page.totalCount,
						variations: page.variations,
						hasMore: page.variations.length > 0,
						allPages: Math.ceil(page.totalCount / query.size)
					}));
					setLoading(false);
				}
			})
			.catch(err => {
				if (mount) {
					setPages(prevPage => ({
						...prevPage,
						hasMore: false
					}));
					setLoading(false);
				}
			});
		return () => {
			mount = false;
		};
	}, [query, menu.id]);

	useEffect(() => {
		if (pages.variations.length > 0) {
			let p = [];
			pages.variations.map(page =>
				p.push({
					id: page.variation.id,
					name: page.variation.name,
					image: page.variation.images[0].urls.original
				})
			);
			store.dispatch(setStatePages(p));
		}
	}, [pages.variations]);

	return (
		<>
			<div className="product-toolbox">
				<ToolboxThree
					setCols={setCols}
					setQuery={setQuery}
					cols={cols}
					pages={pages}
				/>
			</div>
			<FilterCanceler item={query} setQuery={setQuery} />
			{loading ? (
				<SimpleShimmerProducts columns={units[column] / 4} />
			) : (
				<div className="products mb-3">
					{pages.variations.length === 0 ? (
						<div className="row">
							<h4>{t("nothing-found")}</h4>
						</div>
					) : (
						<Fragment>
							<div className="row">
								{pages.variations.map(product => (
									<div
										className={`${grid[cols]} d-flex`}
										key={product.variation.id}
									>
										{cols === "12cols" ? (
											<ProductCardRow
												product={product}
												type="sidebar"
												onAddToCart={dataProduct => {
													onAddToCart(dataProduct);
												}}
											/>
										) : (
											<ProductCard
												product={product}
												type="sidebar"
												onAddToCart={dataProduct => {
													onAddToCart(dataProduct);
												}}
											/>
										)}
									</div>
								))}
							</div>

							<LoadMoreButton
								pages={pages}
								fetchMoreData={fetchMoreData}
							/>
							<QuickView />
							<ChooseVariationModal
								setModal={setModal}
								modal={modal}
								addToCart={addToCart}
							/>
						</Fragment>
					)}
				</div>
			)}
			<div className="page-pagination">
				<Pagination
					pageCount={pages.allPages}
					onChange={({ selected }) => {
						setQuery(prev => ({
							...prev,
							page: 1 + selected
						}));
					}}
				/>
			</div>
		</>
	);
}

export default MarketProductList;
