import React from "react";
import { Link } from "react-router-dom";
import { connect } from "react-redux";
import { useTranslation } from "react-i18next";
import { dt } from "../../i18n";

function Breadcrumb(props) {
	const { t } = useTranslation();

	return (
		<nav
			aria-label="breadcrumb"
			className="breadcrumb-nav breadcrumb-nav-two"
		>
			<p className="title">Вы находитесь здесь</p>
			<ul className="breadcrumb">
				<li className="breadcrumb-item home">
					<span className="circle"></span>
					<Link to="/">{t("home")}</Link>
				</li>
				{props.breadcrumb.map((crumb, index) => (
					<li
						key={index}
						className="breadcrumb-item"
						style={{
							paddingLeft: `${(index + 1) * 15}px`
						}}
					>
						<span className="circle"></span>
						<Link to={`${crumb.link}`}>{dt(crumb.name)}</Link>
					</li>
				))}
			</ul>
		</nav>
	);
}
function mapStateToProps(state, props) {
	return {
		pages: state.any.pages ? state.any.pages : [],
		props: props
	};
}

export default connect(mapStateToProps)(Breadcrumb);
