import QueryString from "qs";
import React, { useRef, useState } from "react";
import { useSelector } from "react-redux";
import MarketProductList from "./MarketProductLists";
import { deepEqual } from "../../utils/array";
import FilterSide from "../category/filterSidebar/filters/FilterSideFour";
import { useTranslation } from "react-i18next";
import { dt } from "../../i18n";
export default function Markets() {
	const { t } = useTranslation();
	let timeOut = setTimeout(() => {}, 1000);
	const { top } = useSelector(state => state.menu);
	const [market, setMarket] = useState(top[0]);
	const [activeMarketChild, setActiveMarketChild] = useState(
		market.children[0]
	);
	const initialQuery = { page: 1, size: 12 };
	const [query, setQuery] = useState(
		window.location.search.length > 0
			? QueryString.parse(window.location.search.slice(1))
			: { ...initialQuery }
	);
	const KEEP_TIME = 1000;
	const myRef = useRef(null);
	const executeScroll = () =>
		myRef.current.scrollIntoView({ behavior: "smooth" });

	function setRegion(e) {
		const region = top.find(m => m.id === parseInt(e.target.value));
		setMarket(region);
		if (region.children.length === 0) {
			setActiveMarketChild(region);
		}
	}

	return (
		<div className="markets-one category-page-5 mt-3">
			<div className="container">
				<div className="markets-header">
					<div className="d-flex align-items-center justify-content-between">
						<h2 className="mb-0">{t("bazaars")}</h2>
						<label
							className="d-flex align-items-center"
							htmlFor="markets-label"
						>
							<p
								className="mb-0 mr-5"
								style={{ whiteSpace: "nowrap" }}
							>
								{t("choose_region")}:
							</p>
							<select
								id="markets-label"
								className="form-control mb-0"
								onChange={e => setRegion(e)}
							>
								{top.map(market => {
									return (
										<option
											key={market.id}
											value={market.id}
										>
											{dt(market.name)}
										</option>
									);
								})}
							</select>
						</label>
					</div>
					<ul className="market-children d-flex align-items-center mt-3">
						{market.children.length
							? market.children.map(market => {
									return (
										<li
											className={`children-item ${
												market === activeMarketChild
													? "active"
													: ""
											}`}
											key={market.id}
											onClick={() =>
												setActiveMarketChild(market)
											}
										>
											{dt(market.name)}
										</li>
									);
							  })
							: null}
					</ul>
				</div>
				<div className="row">
					<div className="col-lg-3 col-0">
						<FilterSide
							onSubmit={values => {
								clearTimeout(timeOut);
								timeOut = setTimeout(() => {
									if (!deepEqual(query, values)) {
										setQuery(prevQuery => ({
											...prevQuery,
											...values,
											page: 1
										}));
									}
								}, KEEP_TIME);
							}}
							clearAll={() => {
								setQuery(initialQuery);
							}}
							menus={top}
							menu={market}
							adClass="sidebar sidebar-shop"
							executeScroll={executeScroll}
							initialValues={query}
						/>
					</div>
					<div ref={myRef} className="col-lg-9 col-12 mb-5">
						<MarketProductList
							query={query}
							setQuery={setQuery}
							menu={activeMarketChild}
							column="4cols"
							title={dt(activeMarketChild.name)}
							executeScroll={executeScroll}
						/>
					</div>
				</div>
			</div>
		</div>
	);
}
