import React from "react";
const initialQuery = { page: 1, size: 12 };

function FilterCancelButton({ setQuery, item }) {
	if (!item.name) {
		return "";
	}
	return (
		<div className="canceler-button default">
			<span>{item.name + ": " + item.value}</span>
			<button
				onClick={e => {
					e.preventDefault();
					setQuery({ ...item.cancelState, ...initialQuery });
				}}
			>
				<i
					style={{ color: "#fff", fontSize: "16px" }}
					className="i-icon icon-times-circle-o"
				></i>{" "}
			</button>
		</div>
	);
}

export default FilterCancelButton;
