import React, { useState, useEffect } from "react";
import Filters from "./Filters";
import ShimmerProduct from "../../../../components/features/loaders/shimmer";
import { getFilterOfMenu } from "../../../../api/menu";
import { Field, reduxForm } from "redux-form";
import { setFiltersData } from "../../../../actions/any";
import CategorySidebar from "./category-sidebar";
import { useTranslation } from "react-i18next";
import "rc-slider/assets/index.css";
import market from "../../../../config/market";
import BreadcrumbTwo from "../../../../components/common/BreadcrumbTwo";

const emptyFilters = {
	variationProperties: [],
	productProperties: [],
	suppliers: [],
};

function counter({ input, className }) {
	return (
		<input
			{...input}
			className={className}
			onChange={(e) => {
				input.onChange(e.target.value);
			}}
		/>
	);
}

function FilterSideFour(props) {
	const menu = props.menu;
	const [toggle, setToggle] = useState(false);
	const [loading, setLoading] = useState(true);
	const [filters, setFilters] = useState({});
	const { t } = useTranslation();

	useEffect(() => {
		window.addEventListener("resize", resizeHandle);
		resizeHandle();

		return () => {
			window.removeEventListener("resize", resizeHandle);
		};
	}, []);

	function resizeHandle() {
		if (window.document.querySelector("body")?.offsetWidth < 992)
			setToggle(true);
		else setToggle(false);
	}

	function hideSideBar() {
		if (
			window.document
				.querySelector("body")
				.classList.contains("sidebar-filter-active")
		)
			window.document
				.querySelector("body")
				.classList.remove("sidebar-filter-active");
	}

	function toggleSideBar() {
		if (
			window.document
				.querySelector("body")
				.classList.contains("sidebar-filter-active")
		)
			window.document
				.querySelector("body")
				.classList.remove("sidebar-filter-active");
		else
			window.document
				.querySelector("body")
				.classList.add("sidebar-filter-active");
	}

	useEffect(() => {
		setLoading(true);
		let mount = true;
		getFilterOfMenu(props.menu.collection)
			.then((res) => {
				if (mount) {
					props.dispatch(setFiltersData(res));
					setFilters(res);
					setLoading(false);
				}
			})
			.catch((err) => {
				if (mount) {
					setFilters({ ...emptyFilters });
					setLoading(false);
				}
			});
		return () => {
			mount = false;
		};
	}, [props.menu.collection]);

	if (loading) {
		return <ShimmerProduct />;
	}

	const { handleSubmit } = props;

	return (
		<form onSubmit={handleSubmit}>
			<aside
				className={toggle ? "sidebar-filter" : "sidebar sidebar-shop"}
			>
				<BreadcrumbTwo
					breadcrumb={props.menus.map((menu) => ({
						link: menu.slug,
						name: menu.name,
					}))}
				/>
				<div className={toggle ? "sidebar-filter-wrapper w-100" : ""}>
					<div
						className="filter-price"
						style={
							market.config.home !== 6
								? {
										margin: "30px 0",
										padding: "0 30px",
								  }
								: null
						}
					>
						<h4>{t("price-from-to")}</h4>
						<div className="d-flex justify-content-between">
							<Field
								name="sellPrice[min]"
								type="number"
								className="form-control mb-0 mr-4"
								placeholder={t("from-two")}
								component={counter}
							/>
							<Field
								name="sellPrice[max]"
								type="number"
								className="form-control mb-0"
								placeholder={t("to")}
								component={counter}
							/>
							<button className="btn ml-4 p-0">
								{t("accepted")}
							</button>
						</div>
					</div>
					{menu.children.length > 0 && market.config.home !== 4 && (
						<CategorySidebar menu={menu} />
					)}
					{filters.variationProperties !== [] ? (
						<Filters
							filters={[
								"properties",
								Object.entries(filters.variationProperties),
							]}
						/>
					) : null}
					{filters.productProperties.length !== [] ? (
						<Filters
							filters={[
								"productProperties",
								Object.entries(filters.productProperties),
							]}
						/>
					) : null}
				</div>
			</aside>
			{toggle ? (
				<button
					className="sidebar-fixed-toggler"
					onClick={toggleSideBar}
				>
					<span className="d-none">{t("filter")}</span>
					<svg
						id="icon"
						width="20px"
						fill="white"
						viewBox="0 0 48 48"
						xmlns="http://www.w3.org/2000/svg"
					>
						<title />
						<path d="M47,12a2,2,0,0,0-2-2H24a2,2,0,0,0,0,4H45A2,2,0,0,0,47,12Z" />
						<path d="M3,14H8.35a6,6,0,1,0,0-4H3a2,2,0,0,0,0,4Zm11-4a2,2,0,1,1-2,2A2,2,0,0,1,14,10Z" />
						<path d="M45,22H37.65a6,6,0,1,0,0,4H45a2,2,0,0,0,0-4ZM32,26a2,2,0,1,1,2-2A2,2,0,0,1,32,26Z" />
						<path d="M22,22H3a2,2,0,0,0,0,4H22a2,2,0,0,0,0-4Z" />
						<path d="M45,34H28a2,2,0,0,0,0,4H45a2,2,0,0,0,0-4Z" />
						<path d="M18,30a6,6,0,0,0-5.65,4H3a2,2,0,0,0,0,4h9.35A6,6,0,1,0,18,30Zm0,8a2,2,0,1,1,2-2A2,2,0,0,1,18,38Z" />
					</svg>
				</button>
			) : null}
			<div className="sidebar-filter-overlay" onClick={hideSideBar} />
		</form>
	);
}

export default reduxForm(
	{
		form: "mainfilterForm",
		onChange: (values, dispatch, props, previousValues) => {
			props.submit();
		},
		enableReinitialize: true,
	},
	{ setFiltersData }
)(FilterSideFour);
