import React from "react";
import { SlideToggle } from "react-slide-toggle";
import { dt } from "../../../../i18n";
import MediaImage from "../../../../components/features/product/MediaImage";
import { Link } from "react-router-dom";
import { useTranslation } from "react-i18next";

export default function CategorySideBar(menu) {
	menu = menu.menu;

	function changeIcon(e) {
		e.target.classList.toggle("collapsed");
	}

	const { t } = useTranslation();
	return (
		<>
			<SlideToggle collapsed={true} key={menu.id}>
				{({ onToggle, setCollapsibleElement }) => (
					<div className="widget widget-collapsible first mb-0">
						<div className="widget-title mb-0">
							<h6
								className="collapsed"
								onClick={(e) => {
									onToggle(e);
									changeIcon(e);
									e.preventDefault();
								}}
							>
								<b>{t("category")}</b>
							</h6>
						</div>
						<div
							className="widget-side"
							ref={setCollapsibleElement}
						>
							<div className="widget-body pl-3 widget-body-filter-top d-block">
								{menu.children.map((subMenu) => (
									<SlideToggle
										collapsed={true}
										key={subMenu.id}
									>
										{({
											onToggle,
											setCollapsibleElement,
										}) => (
											<div
												className={`${
													subMenu.children.length > 0
														? "widget widget-collapsible m-0"
														: "py-2"
												}`}
											>
												{subMenu.children.length > 0 ? (
													<p
														style={{
															margin: "10px 0",
															fontSize: "14px",
														}}
														className="widget-title"
													>
														<a
															href={`category/${subMenu.slug}`}
															onClick={(e) => {
																onToggle(e);
																changeIcon(e);
																e.preventDefault();
															}}
															role="button"
															aria-expanded="true"
															aria-controls="widget-1"
														>
															{dt(subMenu.name)}
															<Link
																to={`${subMenu.slug}`}
															>
																<svg
																	width="16"
																	height="16"
																	viewBox="0 0 16 16"
																	fill="none"
																	xmlns="http://www.w3.org/2000/svg"
																>
																	<path
																		fillRule="evenodd"
																		clipRule="evenodd"
																		d="M14 8C14 11.3137 11.3137 14 8 14C4.68629 14 2 11.3137 2 8C2 4.68629 4.68629 2 8 2C11.3137 2 14 4.68629 14 8ZM16 8C16 12.4183 12.4183 16 8 16C3.58172 16 0 12.4183 0 8C0 3.58172 3.58172 0 8 0C12.4183 0 16 3.58172 16 8ZM8 10C9.10457 10 10 9.10457 10 8C10 6.89543 9.10457 6 8 6C6.89543 6 6 6.89543 6 8C6 9.10457 6.89543 10 8 10Z"
																		fill="#CBCBD3"
																	/>
																</svg>
															</Link>
														</a>
													</p>
												) : (
													<p
														style={{
															display: "flex",
															alignItems:
																"center",
															fontSize: "14px",
															marginBottom: "0",
														}}
													>
														<div
															className="sub-menu-img"
															style={{
																marginRight:
																	"10px",
															}}
														>
															<MediaImage
																image={
																	subMenu.image
																}
																largeSize="50x_"
																mediumSize="50x_"
																smallSize="50x_"
																alt="menu-image"
															/>
														</div>
														<a
															href={`category/${subMenu.slug}`}
															className="collapsed second"
															role="button"
															aria-expanded="true"
															aria-controls="widget-1"
														>
															{dt(subMenu.name)}
														</a>
													</p>
												)}
												{subMenu.children.length >
													0 && (
													<div
														ref={
															setCollapsibleElement
														}
													>
														<div className="widget-body p-0">
															{subMenu.children.map(
																(
																	subSubMenu
																) => (
																	<p
																		key={
																			subSubMenu.id
																		}
																		className="d-flex align-items-center"
																	>
																		<div
																			className="sub-menu-img"
																			style={{
																				marginRight:
																					"10px",
																			}}
																		>
																			<MediaImage
																				image={
																					subSubMenu.image
																				}
																				largeSize="50x_"
																				mediumSize="50x_"
																				smallSize="50x_"
																				alt="menu-image"
																			/>
																		</div>
																		<a
																			href={`category/${subSubMenu.slug}`}
																			role="button"
																			aria-expanded="true"
																			aria-controls="widget-1"
																			className="m-0"
																		>
																			{dt(
																				subSubMenu.name
																			)}
																		</a>
																	</p>
																)
															)}
														</div>
													</div>
												)}
											</div>
										)}
									</SlideToggle>
								))}
							</div>
						</div>
					</div>
				)}
			</SlideToggle>
		</>
	);
}
